
import { defineComponent, reactive } from "vue";

export default defineComponent({
  props: {
    left: Number,
    right: Number,
  },
  setup(props) {
    const style = reactive({
      left: {
        width: `${props.left}%`,
      },
      right: {
        width: `${props.right}%`,
      },
    });

    return { style };
  },
});
